* {
  box-sizing: border-box;
}
body {
  margin: 0;
}
body {
  background-color: #0b2434;
  font-family: Helvetica, Arial, sans-serif;
  padding: 20px;
  font-family: 'Karla', sans-serif;
  font-weight: 700;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
main {
  height: 400px;
  max-width: 600px;
  background-color: whitesmoke;
  padding: 1rem;
  margin-inline: auto;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
h1 {
  font-size: 40px;
  margin: 0;
}
p {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  margin-top: 0;
  text-align: center;
}
.diceWrapper {
  border-radius: 10px;
  height: 50px;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-shadow: 0px 3.57447px 3.57447px rgba(0, 0, 0, 0.1);
}
.diceContainer {
  display: grid;
  gap: 20px;
  grid-template: auto auto / repeat(5, 1fr);
  margin-bottom: 40px;
}
.diceNum {
  font-size: 2em;
  font-weight: lighter;
}
.rollDiceButton {
  height: 50px;
  width: 150px;
  border: none;
  border-radius: 6px;
  background-color: #5035FF;
  color: white;
  cursor: pointer;
  font-size: 1.2rem;
  box-shadow: 0px 5px 12px rgba(0, 0, 0, 0.18);
}
.rollDiceButton:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}
.rollDiceButton:focus {
  outline: none;
}
